.wrapper
    background: url(../img/background-img.jpg) no-repeat center
    position: relative
    width: 100%
    height: auto

.menu__blur
    background: rgba(20, 20, 20, 0.2)
    backdrop-filter: blur(13px)
    max-width: 100%
    max-height: 50%

.menu__lvl1
    position: relative
    overflow: hidden
    padding-top: 60px
    padding-bottom: 40px
    display: flex
    align-items: center
    justify-content: space-between
    color: #ffff

.menu__text
    font-size: 13px
    flex-basis: 22%
    max-width: 22%

.menu__logo
    flex-basis: 10%
    max-width: 10%

.menu__address
    display: flex
    flex-basis: 20%
    max-width: 20%
    grid-gap: 20px
    .adress__text
        font-size: 16px

.menu__phone
    display: flex
    flex-basis: 30%
    max-width: 30%
    grid-gap: 30px
    .phone__text
        font-size: 30px

.menu__btn
    color: #F8B043